<template>
  <div>
    <el-form v-model='getRuleForm'>
      <el-row>
        <el-col :span='5'>
          <el-form-item label='车场名称' class='laeblStyle'>
                <el-select v-model='getRuleForm.parkingId' class='handle-input' v-show='parkingIdOptionsShow' clearable>
                    <el-option
                    v-for='item in parkingIdOptions'
                    :key='item.id'
                    :label='item.label'
                    :value='item.value'>
                    </el-option>
                </el-select>
             </el-form-item>
        </el-col>
        <el-col :span='5'>
          <el-form-item label='车牌号' class='laeblStyle'>
            <el-input v-model='getRuleForm.plateNo' class='handle-input' clearable></el-input>
          </el-form-item>
        </el-col>
        <el-col :span='5'>
          <el-form-item label='车牌颜色' prop='plateColor' class='laeblStyle'>
              <el-select v-model='detailMsgForm.plateColorName' class='handle-input' clearable v-if='plateColorOptionsShow'>
                <el-option
                v-for='item in plateColorOptions'
                :key='item.id'
                :label='item.label'
                :value='item.value'>
                </el-option>
              </el-select>
            </el-form-item>
      </el-col>
  
        <el-col :span='2'>&nbsp;</el-col>
          <el-col :span='2'>
              <el-button type='button' class='el-button el-button--primary' @click='addBlackList(detailMsgForm)'>
                  新增
                </el-button>
          </el-col>
          <el-col :span='2'>
              <el-button type='button' class='el-button el-button--primary' @click='getCarMsg()'>
                  查询
                </el-button>
          </el-col>
      </el-row>
    </el-form>
    <!-- 添加表格 -->
    <el-table
    :data="tableCar"
    style="width: 100%;padding:20px">
    <el-table-column
      prop="id"
      label="id">
    </el-table-column>
    <el-table-column
      prop="parkingName"
      label="车场名称">
    </el-table-column>
    <el-table-column
      prop="plateNo"
      label="车牌号">
    </el-table-column>
    <el-table-column
      prop="plateColorName"
      label="车牌颜色">
    </el-table-column>
    <el-table-column
      prop="remark"
      label="备注">
    </el-table-column>
    <!-- 操作 -->
    <el-table-column
    label="操作" width='300'>
    <template slot-scope="scope">
        <el-button
            @click="handleChangeMsg(scope.$index, scope.row)"
            type="primary"
            size="small">查看
        </el-button>
        <el-button
            @click="handleDetailMsg(scope.$index, scope.row)"
            type="primary"
            size="small">编辑
        </el-button>
        <el-button
            @click="handleDelMsg(scope.$index, scope.row)"
            type="primary"
            size="small">删除
        </el-button>
    </template>
    </el-table-column>
  </el-table> 
  <!-- 分页插件 -->
  <el-pagination
    background
    layout='total,sizes,prev,pager,next,jumper'
    :total='total' v-show='paginationShow'
    style="margin-top:10px;"
    :page-size='pageSize'
    :page-sizes="[5,10,20,30]"
    @size-change="handleSizeChange"
    @current-change='currentChange'
    :current-page='currentPage'>
  </el-pagination>
  <!-- 详情模态框 -->
  <el-dialog title='详情信息' :visible.sync='dialogVisible2' width='1100px'>
      <el-card class='box-card box-card-ex'>
          <el-form v-model='detailMsgForm' disabled>
              <el-row>
                <el-col :span='7'>
                      <el-form-item label='车场名称'>
                          <el-select v-model='detailMsgForm.parkingId' class='handle-input' 
                          v-show='parkingIdOptionsShow' clearable  >
                              <el-option
                              v-for='item in parkingIdOptions'
                              :key='item.id'
                              :label='item.label'
                              :value='item.value'>
                              </el-option>
                          </el-select>
                        </el-form-item>
                  </el-col>
                  <el-col :span='1'>
                    &nbsp;
                  </el-col>
                  <el-col :span='7'>
                        <el-form-item label='车牌号' class='laeblStyle'>
                          <el-input v-model='detailMsgForm.plateNo' class='handle-input'>
                          </el-input>
                        </el-form-item>
                  </el-col>
                  <el-col :span='1'>
                      &nbsp;
                    </el-col>
                    <el-col :span='7'>
                        <el-form-item label='车牌颜色' prop='plateColor' class='laeblStyle'>
                            <el-select v-model='detailMsgForm.plateColor' class='handle-input' v-if='plateColorOptionsShow'>
                              <el-option
                              v-for='item in plateColorOptions'
                              :key='item.id'
                              :label='item.label'
                              :value='item.value'>
                              </el-option>
                            </el-select>
                          </el-form-item>
                    </el-col>
              </el-row>
              <el-row>
                <el-col :span='1'>
                    &nbsp;
                  </el-col>
              </el-row>
              <!-- 第三行 -->
              <el-row>
                <el-col :span='10'>
                  <el-form-item label='备注' style='text-align:right;' >
                    <el-input type='textarea' :rows='2' v-model='detailMsgForm.remark' style="width:75%;">
                    </el-input>
                  </el-form-item>
                </el-col>
      
              </el-row>
            </el-form>
          </el-card>
  </el-dialog>
  <!-- 编辑模态框 -->
  <el-dialog title='详情信息' :visible.sync='dialogVisible3' width='1100px'>
      <el-card class='box-card box-card-ex'>
          <el-form v-model='changeMsgForm'>
              <el-row>
                <el-col :span='7'>
                      <el-form-item label='车场名称'>
                          <el-select v-model='changeMsgForm.parkingId' class='handle-input' 
                          v-show='parkingIdOptionsShow' clearable  >
                              <el-option
                              v-for='item in parkingIdOptions'
                              :key='item.id'
                              :label='item.label'
                              :value='item.value'>
                              </el-option>
                          </el-select>
                        </el-form-item>
                  </el-col>
                  <el-col :span='1'>
                    &nbsp;
                  </el-col>
                  <el-col :span='7'>
                        <el-form-item label='车牌号' class='laeblStyle'>
                          <el-input v-model='changeMsgForm.plateNo' class='handle-input'>
                          </el-input>
                        </el-form-item>
                  </el-col>
                  <el-col :span='1'>
                      &nbsp;
                    </el-col>
                    <el-col :span='7'>
                        <el-form-item label='车牌颜色' prop='plateColor' class='laeblStyle'>
                            <el-select v-model='changeMsgForm.plateColorName' class='handle-input' v-if='plateColorOptionsShow'>
                              <el-option
                              v-for='item in plateColorOptions'
                              :key='item.id'
                              :label='item.label'
                              :value='item.value'>
                              </el-option>
                            </el-select>
                          </el-form-item>
                    </el-col>
              </el-row>
              <!-- 第三行 -->
              <el-row>
                <el-col :span='10'>
                  <el-form-item label='备注' >
                    <el-input type='textarea' :rows='2' v-model='changeMsgForm.remark' style="width:70%;margin-left:20px;">
                    </el-input>
                  </el-form-item>
                </el-col>
      
              </el-row>
                <!-- 按钮行 -->
                  <div style='float:right;margin:20px 5% 0 0;'>
                      <el-button class="el-button el-button--warning" style='margin-right:30px;width:80px' @click='cancel3()'>取消</el-button>
                    <el-button class="el-button el-button--primary" style='width:80px' @click='saveInsertMsg3(changeMsgForm)'>保存29</el-button>
                  </div>
                  <div style="clear:both"></div>
            </el-form>
          </el-card>
  
  
    </el-dialog>
  <!-- 新增窗口模态框 -->
  <el-dialog title='新增白名单' :visible.sync="dialogVisibleAddRule" width='60%' >
    <el-card class='box-card box-card-ex'>
      <el-form v-model='addMsgForm'>
          <el-row>
            <el-col :span='7'>
                  <el-form-item label='车场名称'>
                      <el-select v-model='addMsgForm.parkingId' class='handle-input' 
                      v-show='parkingIdOptionsShow' clearable  >
                          <el-option
                          v-for='item in parkingIdOptions'
                          :key='item.id'
                          :label='item.label'
                          :value='item.value'>
                          </el-option>
                      </el-select>
                    </el-form-item>
              </el-col>
              <el-col :span='1'>
                &nbsp;
              </el-col>
              <el-col :span='7'>
                    <el-form-item label='车牌号' class='laeblStyle'>
                      <el-input v-model='addMsgForm.plateNo' class='handle-input'>
                      </el-input>
                    </el-form-item>
              </el-col>
              <el-col :span='1'>
                  &nbsp;
                </el-col>
                <el-col :span='7'>
                    <el-form-item label='车牌颜色' prop='plateColor' class='laeblStyle'>
                        <el-select v-model='addMsgForm.plateColor' class='handle-input' v-if='plateColorOptionsShow'>
                          <el-option
                          v-for='item in plateColorOptions'
                          :key='item.id'
                          :label='item.label'
                          :value='item.value'>
                          </el-option>
                        </el-select>
                      </el-form-item>
                </el-col>
          </el-row>
          <!-- 第三行 -->
          <el-row>
            <el-col :span='10'>
              <el-form-item label='备注' >
                <el-input type='textarea' :rows='2' v-model='addMsgForm.remark' style="width:70%;margin-left:20px;">
                </el-input>
              </el-form-item>
            </el-col>
  
          </el-row>
            <!-- 按钮行 -->
              <div style='float:right;margin:20px 5% 0 0;'>
                  <el-button class="el-button el-button--warning" style='margin-right:30px;width:80px' @click='cancel2()'>取消</el-button>
                <el-button class="el-button el-button--primary" style='width:80px' @click='saveInsertMsg(addMsgForm)'>保存77</el-button>
              </div>
              <div style="clear:both"></div>
        </el-form>
      </el-card>
  </el-dialog>
  
  </div>
  </template>
  <script>
    import moment from 'moment'
    import qs from 'qs'
    export default {
      methods: {
        handleDelMsg(index,row){
          console.log(index)
          console.log(row)
          var self=this;
          var url=self.baseUrl+"whiteList/delete?id="+row.id;
          self.$confirm('此操作将永久删除该记录, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          self.$axios.get(url).then(function(res){
            console.log("白名单。。")
            console.log(res.data)
          if(res.data.status=='0'){
            self.$message({
              message:res.data.message,
              type:"success"
            })
            self.getCarMsg();
          }else{
            self.$message({
              message:res.data.message,
              type:"info"
            })
          }
          }).catch(err=>console.log(err))
        }).catch(() => {
          self.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });




        },
        editHMD(val){
          var self=this;
          if(val=='1'){
            self.editHMD1=true;
          }else{
            self.editHMD1=false;
          }
        },
        hmdMsg(val){
          var self=this;
          console.log(val)
          if(val=='1'){
            self.qfShow=true;
          }else{
            self.qfShow=false;
          }
        },
        //黑名单管理
        saveInsertMsg3(val){
          var self=this;
          var params;
          self.pdCarColor(val.plateColor);
          console.log("val==================")
          var plateColorName;
          if(!(Number(val.plateColorName))){
            plateColorName=val.plateColorName
          }else{
            plateColorName=self.plateColorOptions[val.plateColorName-1].label
          }
          console.log(plateColorName)
              params={
                "id":val.id,
              "parkingId":val.parkingId,
              "plateNo":val.plateNo,
              "plateColorName":plateColorName,
              "remark":val.remark
              }
          var url=self.baseUrl+'whiteList/update';
          self.$axios.post(url,params).then(function(res){
            console.log(res.data)
            if(res.data.status=='0'){
              self.$message({
                message:res.data.message,
                type:"success"
              })
              self.dialogVisible3=false;
              self.getCarMsg();
            }else{
              self.$message({
                message:res.data.message,
                type:"info"
              })
            }
          }).catch(err=>console.log(err))
        },
        // 车牌颜色
      getCarColor(){
        var self=this;
        var url=self.baseUrl+"vehicleInfo/plateColorList"
        self.$axios.get(url).then(function(res){
          self.plateColorOptionsShow=true;
          if(res.data.status=='0'){
            for(var i=0;i<res.data.data.length;i++){
              self.plateColorOptions[i]={
                id:res.data.data[i].id,
                label:res.data.data[i].plateColor,
                value:res.data.data[i].plateColorNo,
              }
            }
          }
        }).catch(function(err){
          console.log(err);
        })
      },
      pdCarColor(val){
        if(!Number(val)){
          return false;
        }else{
          return true;
        }
      },
        addBlackList(val){
          var self=this;
          self.dialogVisibleAddRule=true;
        },
        handleBJ(val){
          console.log(val);
          var self=this;
          var url=self.baseUrl+"feeRule/update";
          val.feeRuleConfigs=val.scArr;
          self.$axios.post(url,val).then(function(res){
            console.log(res.data)
            self.$message({
              message:res.data.message,
              type:"info"
            })
            self.dialogVisible3=false;
          }).catch(err=>console.log(err));
        },
        cancelBJ(){
          this.dialogVisible3=false;
        },
        getCycleType(){
          var self=this;
          var url=self.baseUrl+"feeRule/initCycleType"
          self.$axios.get(url).then(function(res){
            self.cycleTypeIdShow=true;
            for(var i=0;i<res.data.data.length;i++){
            self.cycleTypeIdOption[i]={
              id:res.data.data[i].cycleTypeId,
              value:res.data.data[i].cycleTypeId,
              label:res.data.data[i].cycleTypeName,
            }
            }
            console.log(self.cycleTypeIdOption)
          }).catch(err=>console.log(err))
        },
        saveEditMsg(val){
          var self=this;
          var url=self.baseUrl+"feeRule/updateRuleForVehicleType";
          var params={
            "parkingId":self.changeMsgForm.parkingId,
            "id":self.changeMsgForm.id,
            "vehicleTypeId":self.changeMsgForm.vehicleTypeId,
            "feeRuleId":self.changeMsgForm.feeRuleId,
          }
          self.$axios.post(url,params).then(function(res){
            console.log(res.data)
            self.$message({
              message:res.data.message,
              type:"info"
            })
            self.dialogVisible3=false;
          }).catch(function(err){
            console.log(err);
          })
        },
        cancel2(){
          this.dialogVisibleAddRule=false;
        },
        cancel3(){
          this.dialogVisible3=false;
        },
        saveInsertMsg(val){
          var self=this;
          var url=self.baseUrl+"whiteList/add";
          console.log("看下数据")
          console.log(val.plateColor)
          var a=self.pdCarColor(val.plateColor);
          if(a){
          }else{
            self.$message({
              message:"请选择车牌颜色!",
              type:"info"
            })
            return;
          }
          var params={
            "parkingId":self.addMsgForm.parkingId,
            "plateNo":self.addMsgForm.plateNo,
            "plateColorName":self.plateColorOptions[(self.addMsgForm.plateColor)-1].label,
            "remark":self.addMsgForm.remark,
          };
          self.$axios.post(url,params).then(function(res){
            console.log(res.data)
            if(res.data.status=='0'){
              self.$message({
                message:res.data.message,
                type:"success"
              })
              self.dialogVisibleAddRule=false;
              self.search();
            }else{
              self.$message({
                message:res.data.message,
                type:"info"
              })
            }
          }).catch(function(err){
            console.log(err);
          })
        },
        addMsg(){
          var self=this;
          self.dialogVisibleAddRule=true;
        },
        //下拉列表事件
        sendParkId(val){
          console.log(val)
          this.ruleType(val);
        },
        // 收费类型
        ruleType(val){
          var self=this;
          self.feeRuleIdOptionsShow=false;
          var url=self.baseUrl+"feeRule/initRuleList/"+val
          self.$axios.get(url).then(res=>{
            console.log('ruleType   res')
            console.log(res.data.data)
            self.feeRuleIdOptionsShow=true;
            for(var i=0;i<res.data.data.length;i++){
              self.feeRuleIdOptions[i]={
                id:res.data.data[i].id,
                label:res.data.data[i].feeRule,
                value:res.data.data[i].id
              }
            }
            console.log(self.feeRuleIdOptions)
          }).catch(err=>{
            console.log(err);
          })
        },
        search(){
          var self=this;
          var params={
            "parkingId":self.getRuleForm.parkingId,
            "currentPage":self.currentPage,
            "pageSize":self.pageSize,
            "plateNo":self.getRuleForm.plateNo,
              "plateColorName":self.detailMsgForm.plateColor
          };
          self.axiosMsg(params);
        },
        cancel(){
          this.dialogVisible3=false;
        },
  
        getParkingId(){
          var self=this;   
        var url=self.baseUrl+"vehicleInfo/initParkingList?userId="+localStorage.getItem("userId")
        self.$axios.get(url).then(function(res){
          self.parkingIdOptionsShow=true;
          if(res.data.status=='0'){
            for(var i=0;i<res.data.data.length;i++){
              self.parkingIdOptions[i]={
                id:res.data.data[i].id,
                label:res.data.data[i].parkingName,
                value:res.data.data[i].id
              }
            }
          }
        }).catch(function(err){
          console.log(err);
        })
        },
        saveMsg(val){
          var self=this;
          console.log('this.detailForm2.plateColorNo');
          console.log(self.detailForm2.vehicleType);
            var params={
              "id":self.row.id,
              "parkingId":self.detailForm2.parkingId,
              "vehicleTypeId":String(self.detailForm2.vehicleType),
              "plateNo":self.detailForm2.plateNo,
              "plateColorNo":self.detailForm2.plateColorNo,
              "plateColor": self.detailForm2.plateColor,
              "vehicleModel":self.detailForm2.vehicleModel,
  
              "vehicleOwner":self.detailForm2.vehicleOwner,
              "ownerIdNo":self.detailForm2.ownerIdNo,
              "ownerGender":self.detailForm2.ownerGender,
              "ownerBirth":self.detailForm2.ownerBirth,
              "ownerPhone":self.detailForm2.ownerPhone,
              "ownerAddress":self.detailForm2.ownerAddress,
              "rfidNo":self.detailForm2.rfidNo,
            }
          self.axiosChangeCar(params);
        },
        axiosChangeCar(params){
          var self=this;
          var url=self.baseUrl+"vehicleInfo/updateVehicle";
          self.$axios.post(url,params).then(function(res){
            console.log('res.data.......');
            console.log(res.data);
          }).catch(function(err){
            console.log(err);
          })              
        },
        currentChange(currentPage){
            this.getTime();
            var self=this;
            this.currentPage=currentPage;
              var url='http://58.210.46.54:8000/listGcjlData';
              var paramString={
            "parkingId":self.getRuleForm.parkingId,
            "currentPage":self.currentPage,
            "pageSize":self.pageSize,
            "plateNo":self.getRuleForm.plateNo,
              "plateColorName":self.detailMsgForm.plateColor
              }
              var jsonParams=JSON.stringify(paramString);
            this.axiosMsg(jsonParams);
        },
        handleSizeChange(val){
            this.pageSize=val;
            var self=this;
            var url='http://58.210.46.54:8000/listGcjlData';
            var str={
              "parkingId":self.getRuleForm.parkingId,
            "currentPage":self.currentPage,
            "pageSize":self.pageSize,
            "plateNo":self.getRuleForm.plateNo,
              "plateColorName":self.detailMsgForm.plateColor
            }
            var jsonParams=JSON.stringify(str);
             this.axiosMsg(jsonParams);
            console.log(`每页显示${val}条`)
        },
        axiosMsg(params){
          var self=this;
          this.paginationShow=true;
          this.$axios({
            method:"post",
            url:self.baseUrl+"whiteList/list",
            headers:{
              "Content-Type":"application/json"
            },
            data:params
          }).then(function(response){
            self.total=response.data.data.total;
            self.tableCar=response.data.data.records;
            console.log("response白名单...")
            console.log(self.tableCar)
          }).catch(function(err){
            console.log(err);
          })
        },
        momentTime(val){
            return moment(val).format("YYYY-MM-DD HH:mm:ss");
        },
        momentTime1(val){
            return moment(val).format("YYYY-MM-DD");
        },
        getCarMsg(){
          var self=this;
            //查询，获取参数
            var str={
              'parkingId':self.getRuleForm.parkingId,
              "pageSize":10,
              "currentPage":1,
              "plateNo":self.getRuleForm.plateNo,
              "plateColorName":self.detailMsgForm.plateColor
            }
            var jsonParams=JSON.stringify(str);
            this.axiosMsg(jsonParams);
        },
        handleDetailMsg(index,row,scope){
          var self=this;
          this.dialogVisible3=true;
          self.sfgzRow=row;
          console.log('row.编辑..')
          console.log(row)
          //调用回显;
          var url=self.baseUrl+"whiteList/detail?id="+row.id;
          self.$axios.get(url).then(function(res){
            self.changeMsgForm=res.data.data;
          })
        },
        handleChangeMsg(index,row,scope){
          var self=this;
          self.dialogVisible2=true;
          var url=self.baseUrl+"whiteList/detail?id="+row.id;
          self.$axios.get(url).then(function(res){
            self.detailMsgForm=res.data.data;
            console.log('测试数据')
            console.log(res.data.data)
            self.detailMsgForm.plateColor=String(res.data.data.plateColorName);
            if(res.data.data.blackType=='1'){
              self.xqShow=true;
            }else{
              self.xqShow=false;
            }
          })
        }
      },
      data() {
        return {
          editHMD1:false,
          xqShow:false,
          qfShow:false,
          blackTypeOptions:[
            {id:1,label:"逃费",value:1},
            {id:2,label:"套牌",value:2},
          ],
          plateColorOptionsShow:false,
          plateColorOptions:[],
          sfgzRow:[],
          cycleTypeIdOption:[],
          scArrShow:false,
          statusOptions:[
            {id:1,value:1,label:"正常"},
            {id:0,value:0,label:"停用"}
          ],
          getRuleForm:{},
          parkingIdOptions:[],
          parkingIdOptionsShow:false,
          feeRuleIdOptionsShow:false,
          feeRuleIdOptions:[],
          vehicleTypeOptions:[],
          vehicleTypeOptionsShow:false,
          currentPage:1,
          total:0,
          tableCar:[],
          paginationShow:false,
          pageSize:10,
          dialogVisible2:false,
          detailMsgForm:{
            scArr:[]
          },
          changeMsgForm:{
            scArr:[]
          },
          dialogVisible3:false,
          addMsgForm:{},
          dialogVisibleAddRule:false,
          rowMsg:{}
        }
      },
      mounted:function(){
          this.getParkingId();
          this.getCycleType();
          this.getCarMsg();
          this.getCarColor();
      }
    }
  </script>
